<template><div>
  <!-- <div class="mx-auto text-center">
    {{hours}}:{{minutes}}:{{seconds}} -->
    <div class="d-flex d-inline d-block mt-1" style="float: center;">
      <b-row class="mx-auto text-center">
        <span class="border border-warning" style="height:70px;width:70px;">
          <p class="text-center" id="show-time"> {{hours}}</p>
        </span>
        <span>
          <p class="text-center mx-1" id="show-colon">:</p>
        </span>
        <span class="border border-warning" style="height:70px;width:70px;">
          <p class="text-center" id="show-time" v-if="minutes<10">0{{minutes}}</p>
          <p class="text-center" id="show-time" v-else>{{minutes}}</p>
        </span>
        <span>
          <p class="text-center mx-1" id="show-colon">:</p>
        </span>
        <span class="border border-warning" style="height:70px;width:70px;">
          <p class="text-center" id="show-time" v-if="seconds<10">0{{seconds}}</p>
          <p class="text-center" id="show-time" v-else>{{seconds}}</p>
        </span>
      </b-row>
    </div>
     <p class="mx-auto text-center">time remaining--</p>
  </div>
</template>

<script>
export default {
  name: 'CountDownTimer',
  data () {
    return {
      hours: 0,
      minutes: 1,
      seconds: 5
    }
  },
  methods: {
    countDown () {
      this.seconds--
      // if (this.minutes < 10) this.minutes = '0' + this.minutes
      // if (this.seconds < 10) this.seconds = '0' + this.seconds
      if (this.seconds < 0) {
        this.seconds = 59
        this.minutes--
        if (this.minutes < 0) {
          this.minutes = 59
          this.hours--
          if (this.hours < 0) {
            this.hours = 23
          }
        }
      }
    },
    initialize () {
      const currentTime = new Date()
      const endOfToday = new Date().setHours(23, 59, 59, 999)
      let diff = Math.floor((endOfToday - currentTime) / 1000)
      this.hours = Math.floor(diff / 3600)
      diff -= this.hours * 3600
      this.minutes = Math.floor(diff / 60)
      this.seconds = diff - this.minutes * 60
    }
  },
  created () {
    this.initialize()
    setInterval(this.countDown, 1000)
  }
}
</script>

<style scoped>
#show-time {
  font-size: 3rem;
}
#show-colon {
  font-size: 3rem;
}
</style>
