const sorter = {
  methods: {
    sortArray (array, property, order) {
      const nestedProperty = property.split('.')
      const getProperty = object => {
        let ret = object
        for (const prop of nestedProperty) {
          ret = ret[prop]
        }
        return ret
      }
      array = array.sort((a, b) => {
        if (getProperty(a) < getProperty(b)) {
          return order === 'desc' ? 1 : -1
        }
        if (getProperty(a) > getProperty(b)) {
          return order === 'desc' ? -1 : 1
        }
        return 0
      })
    }
  }
}

export default sorter
