<template>
  <b-container fluid>
    <b-carousel
      id="carousel-1"
      :interval="4000"
      controls
      indicators
      img-width="200"
      img-height="200"
      background="gray"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
      class="mb-1 mt-4 mx-auto text-center "
    >
    <!-- set width in style of b-carousel to change width -->
      <b-carousel-slide
        v-for="(image1,index) in images" :key="index">
        <template v-slot:img>
          <img width="200"
               height="300"
               background-color="red"
               :src=image1.image.link
               class="d-block w-100"
          >
        </template>
      </b-carousel-slide>
    </b-carousel>
    <marquee style="background-color: lightblue; color: black;">
      <h1>{{text}}</h1>
    </marquee>
    <div class="mx-auto text-center">
      <h1>Deals of the day</h1>
    </div>
    <count-down-timer></count-down-timer>
    <product-sorter :products="products"></product-sorter>
<!--    <div id="txt"></div>-->
    <!-- <div class="">
      <marquee behavior="scroll" scrollamount="20" direction="right to left">
        <h1>{{text}}</h1>
      </marquee>
    </div> -->
    <!-- <pre>{{images}}</pre> -->
    <div style="padding:0 5% 0 5%;">
      <b-row>
        <b-col v-for="(product, idx) in products" :key="idx" lg="2" md="3" sm="4" cols="6" class="my-2">
          <product-box :product="product"></product-box>
        </b-col>
      </b-row>
      <b-container fluid class="d-flex justify-content-center">
        <b-overlay :show="busy" opacity="0.4" spinner-small
                          spinner-variant="primary" class="d-inline block">
          <b-button variant="success" :disabled="busy" v-if="!productLoadingDone" @click="loadMore">
            Load More
          </b-button>
        </b-overlay>
      </b-container>
    </div>
    <hr>
  </b-container>
</template>

<script>
import loader from '../../mixins/loader'
import ProductBox from '../../components/Boxes/ProductBox'
import CountDownTimer from '../../components/CountDownTimer'
import ProductSorter from '../../components/ProductSorter'

export default {
  name: 'FlashSale',
  data () {
    return {
      products: [],
      productLoadingDone: true,
      busy: false,
      images: [],
      sliding: false,
      text: null
    }
  },
  components: {
    ProductBox,
    CountDownTimer,
    ProductSorter
  },
  mixins: [
    loader
  ],
  methods: {
    onSlideStart (slide) {
      this.sliding = true
      this.text = this.images[slide].text
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    startTime () {
      const today = new Date()
      const h = today.getHours()
      let m = today.getMinutes()
      let s = today.getSeconds()
      m = this.checkTime(m)
      s = this.checkTime(s)
      document.getElementById('txt').innerHTML = h + ':' + m + ':' + s
      // eslint-disable-next-line no-unused-vars
      const t = setTimeout(this.startTime, 500)
    },
    checkTime (i) {
      if (i < 10) {
        i = '0' + i
      }
      return i
    },
    loadMore () {
      if (!this.productLoadingDone) this.busy = true
      const offset = this.products.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('products/flash-sale', { offset, limit }, 'flash-sale')
        .then(response => {
          this.products = this.products.concat(response.data.products)
          this.busy = false
          this.productLoadingDone = response.data.products.length < limit
        })
    },
    loadBanner () {
      this.loader('/banner/all', { type: 'flashSale' }, 'banners for flash-sale')
        .then(response => {
          this.images = response.data
        })
    },
    startCallBack (msg) {
      alert(msg)
    },
    endCallBack (msg) {
      alert(msg)
    }
  },
  created () {
    this.loadMore()
    this.loadBanner()
    // this.startTime()
  }
}
</script>

<style scoped>
.marquee {
  height: 50px;
  overflow: hidden;
  position: relative;
  font-style: italic;
  background: green;
  color: white;
  border: 1px solid black;
}

.marquee h1 {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
  /* Starting position */
  -moz-transform:translateX(100%);
  -webkit-transform:translateX(100%);
  transform:translateX(100%);
  /* Apply animation to this element */
  -moz-animation: scroll-left 4s linear infinite;
  -webkit-animation: scroll-left 4s linear infinite;
  animation: scroll-left 4s linear infinite;
}
/* Move it (define the animation) */
@-moz-keyframes scroll-left {
    0%   { -moz-transform: translateX(100%); }
    100% { -moz-transform: translateX(-100%); }
}

@-webkit-keyframes scroll-left {
    0%   { -webkit-transform: translateX(100%); }
    100% { -webkit-transform: translateX(-100%); }
}

@keyframes scroll-left {
 0%   {
    -moz-transform: translateX(100%); /* Browser bug fix */
    -webkit-transform: translateX(100%); /* Browser bug fix */
    transform: translateX(100%);
  }
 100% {
    -moz-transform: translateX(-100%); /* Browser bug fix */
    -webkit-transform: translateX(-100%); /* Browser bug fix */
    transform: translateX(-100%);
  }
}
</style>
