<template>
  <div class="shadow mb-2 text-center">
    <span class="mx-2">Sort by:</span>
    <b-dropdown class="col-2" variant="Light" :text="sortOptions[sortId].text">
      <b-dropdown-item-button
        v-for="(option, index) in sortOptions"
        :key="index"
        @click="setSort(index)"
      >
        {{option.text}}
      </b-dropdown-item-button>
    </b-dropdown>
  </div>
</template>

<script>
import sorter from '../mixins/sorter'

export default {
  name: 'ProductSorter',
  props: [
    'products'
  ],
  data () {
    return {
      sortId: 0,
      sortOptions: [
        {
          text: 'No Order',
          sort: null,
          order: null
        },
        {
          text: 'A to Z',
          sort: 'name',
          order: 'asc'
        },
        {
          text: 'Z to A',
          sort: 'name',
          order: 'desc'
        },
        {
          text: 'Price (Low to High)',
          sort: 'curPrice',
          order: 'asc'
        },
        {
          text: 'Price (High to Low)',
          sort: 'curPrice',
          order: 'desc'
        },
        {
          text: 'Rating (High to Low)',
          sort: 'rating',
          order: 'desc'
        },
        {
          text: 'Rating (Low to High)',
          sort: 'rating',
          order: 'asc'
        },
        {
          text: 'Discount (More to Less)',
          sort: 'discount',
          order: 'desc'
        },
        {
          text: 'Discount (Less to More)',
          sort: 'discount',
          order: 'asc'
        }
      ]
    }
  },
  mixins: [
    sorter
  ],
  methods: {
    setSort (id) {
      if (this.sortId !== id) {
        this.sortId = id
        const sort = this.sortOptions[this.sortId].sort
        const order = this.sortOptions[this.sortId].order
        this.sortArray(this.products, sort, order)
      }
    }
  }
}
</script>

